.footer {
    padding: 60px 0 70px;

    @include media-mobile {
        padding: 31px 0;
    }
}

.footer__wrapper {
    display: flex;

    @include media-tablet {
        flex-direction: column;
    }
}

.footer-navigation {
    flex-grow: 1;
    padding-left: 152px;

    @include media-tablet {
        padding-left: 0;
        margin-top: 34px;
    }

    @include media-mobile {
        flex-direction: column;
    }
}

.footer-navigation__title {
    margin: 0 0 15px;
}

.footer-navigation__links {
    @extend %font-add;
    list-style: none;
    margin: 0;
    padding: 0;

    a {
        @extend %transition;
        @include font(1.6rem, 3.5rem, 400);
        color: $color-text;
        text-decoration: none;

        &:hover {
            color: $color-brand;
        }
    }
}